<template>
  <div class="toolbar">
    <Collapse v-model="currentPanel" accordion>
        <Panel name="1">
            
          <div slot="content">
            <List  class="clearfix child-panel">
              <ListItem draggable="true"  @dragstart="dragstart($event,item)" v-for="item in preFacilities" v-bind:key="item.command" :class="'iconfont '+item.iconName">
                <div draggable="true"  @dragstart="dragstart($event,item)" v-html="item.path" :title="item.text"></div>
              </ListItem>
            </List >
            <div class="panelFootButton">
              <Button style = "margin-right:15px" @click.stop="showSearchDialog=!showSearchDialog">
                
              </Button>
              <Button @click.stop="showFacilityDialog=!showFacilityDialog">
                
              </Button>
            </div>
          </div>
        </Panel>
        <Panel name="2">
            
          <div slot="content">
            <List class="clearfix child-panel">
              <ListItem draggable="true"  @dragstart="dragstart($event,item)" v-for="item in combines" v-bind:key="item.command" :class="'iconfont '+item.name">
                <div draggable="true"  @dragstart="dragstart($event,item)" v-html="item.path" :title="item.text"></div>
              </ListItem>
            </List>
             <div class="panelFootButton">
              <Button @click.stop="showCombinePanel">
                
              </Button>
             </div>
          </div>
        </Panel>
        <Panel name="3">
            
          <div slot="content">
            <List class="clearfix child-panel">
              <ListItem draggable="true"  @dragstart="dragstart($event,item)" v-for="item in preObstacle" v-bind:key="item.command" :class="'iconfont '+item.name">
                <div draggable="true"  @dragstart="dragstart($event,item)" v-html="item.path" :title="item.text"></div>
              </ListItem>
            </List>
             <div class="panelFootButton">
              <Button @click.stop="showObstacleDialog=!showObstacleDialog" >
                
              </Button>
             </div>
          </div>
        </Panel>
        <Panel name="4">
            
          <div slot="content">
            <List class="clearfix child-panel">
             <ListItem draggable="true" v-for="(item,index) in precustomize" v-bind:key="index"  @dragstart="dragstart($event,item)">
               <div draggable="true"  @dragstart="dragstart($event,item)" style="font-size:20px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.roomName"></div>
             </ListItem>
            </List>
             <div class="panelFootButton">
              <Button @click.stop="showCustomize=!showCustomize">
                
              </Button>
             </div>
          </div>
        </Panel>
    </Collapse>

      <!-- <Drawer title="Basic Drawer" :closable="false" v-model="showSearchDialog">
          <p>Some contents...</p>
          <p>Some contents...</p>
          <p>Some contents...</p>
      </Drawer> -->

     <div class="upload-win-child" v-if="showSearchDialog">
      <div class="customize-win-header">
           <div @click.stop="showSearchDialog=false" class="close iconfont icon-guanbi"></div>
      </div>
      <div class="upload-win-content">
          <div style="margin-bottom:20px; font-size:22px; font-weight:bold"><span></span></div>
          <div style="margin-bottom:20px"><input  class="image-url" placeholder="" type="text" text="serchKeyWord" v-model="serchKeyWord"/> <Button @click="clearSearch"></Button></div>
      </div>
    </div>

    <div class="customize-win"  v-if="showFacilityDialog">
        <div class="customize-win-header">
           <span></span>
           <div @click.stop="showFacilityDialog=false" class="close iconfont icon-guanbi"></div>
        </div>

        <div class="customize-image">
            <ul>
             <li draggable="true" v-for="(item,index) in facilities" v-bind:key="index">
               <span @click.stop="removeFacility(item)" class="iconfont icon-guanbi"></span>
               <div style="font-size:12px;max-width:100px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.name"></div>
            </li>
             <li @click.stop="showFacilityDetail"><span class="iconfont icon-add" style="color:#666;font-size:48px;line-height:44px;text-align:center;cursor:pointer"></span></li>
           </ul>
        </div>
    </div>

    <Drawer title="" :closable="false" v-model="facilityDetail.isShow" class="facility-draw">
      <div class="upload-win-content">
          <div style="margin-bottom:10px"><span></span></div>
          <div style="margin-bottom:10px"><span></span><input  class="image-url" placeholder="/" type="text"  v-model="facility.name"/></div>
          <div style="margin-bottom:10px"><span></span><input  class="image-url" placeholder="/" type="text"  v-model="facility.id"/></div>
          <div style="margin-bottom:10px"><span></span><input  class="image-url" placeholder="cm" type="text"  v-model="facility.width"/></div>
          <div style="margin-bottom:10px"><span></span><input  class="image-url" placeholder="cm" type="text"  v-model="facility.height"/></div>
          <div style="margin-bottom:10px"><span></span><input  class="image-url" placeholder=":" type="text"  v-model="facility.cost"/></div>
          <div style="margin-bottom:10px"><span></span></div>
          <div style="margin-bottom:10px"><span></span><input  class="image-url" placeholder="cm0" type="text"  v-model="facility.safe.up"/></div>
          <div style="margin-bottom:10px"><span></span><input  class="image-url" placeholder="cm0" type="text"  v-model="facility.safe.down"/></div>
          <div style="margin-bottom:10px"><span></span><input  class="image-url" placeholder="cm0" type="text"  v-model="facility.safe.left"/></div>
          <div style="margin-bottom:10px"><span></span><input  class="image-url" placeholder="cm0" type="text"  v-model="facility.safe.right"/></div>  
          <div style="margin-bottom:40px">
              <span></span>
              <input type="radio" name="face" v-model="facility.face" value="0" /><label for="up"></label>
              <input type="radio" name="face" v-model="facility.face" value="2" /><label for="down"></label>
              <input type="radio" name="face" v-model="facility.face" value="3" /><label for="left"></label>
              <input type="radio" name="face" v-model="facility.face" value="1" /><label for="right"></label>
         </div>        
      </div>
      <div class="upload-win-footer"  v-show="facilityDetail.showAddButton">
          <button @click="addFacility"></button>
      </div>
    <!-- </div> -->
    </Drawer>



    <div class="customize-win" v-if="showObstacleDialog">
        <div class="customize-win-header">
           <span></span>
           <div @click.stop="showObstacleDialog=false" class="close iconfont icon-guanbi"></div>
        </div>
        <div class="customize-image">
            <ul>
             <li draggable="true" v-for="(item,index) in obstacles" v-bind:key="index">
               <span @click.stop="removeObstacle(item)" class="iconfont icon-guanbi"></span>
               <!-- <img draggable="false" :src="item.image" alt=""> -->
               <div style="font-size:15px;max-width:50px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.name"></div>
            </li>

             <li @click.stop="showObstacleDetail=!showObstacleDetail"><span class="iconfont icon-add" style="color:#666;font-size:48px;line-height:44px;text-align:center;cursor:pointer"></span></li>
           </ul>
        </div>
    </div>

    <Drawer title="" :closable="false" v-model="showObstacleDetail" class="facility-draw">
        <div class="upload-win-content">
            <div style="margin-bottom:20px"><span></span></div>
            <div style="margin-bottom:20px"><span></span><input  class="image-url" placeholder="/" type="text" text="obstacleName" v-model="obstacle.name"/></div>
            <div style="margin-bottom:20px"><span></span><input  class="image-url" placeholder="cm" type="text" text="obstacleWidth" v-model="obstacle.width"/></div>
            <div style="margin-bottom:20px"><span></span><input  class="image-url" placeholder="cm" type="text" text="obstacleHeight" v-model="obstacle.height"/></div>
        </div>
        <div class="upload-win-footer">
            <button @click="addObstacle"></button>
        </div>
    </Drawer>


    <div class="customize-win" v-if="showCustomize">
        <div class="customize-win-header">
           <span></span>
           <div @click.stop="showCustomize=false" class="close iconfont icon-guanbi"></div>
        </div>

        <div class="customize-image">
            <ul>
             <li draggable="true" v-for="(item,index) in customize" v-bind:key="index">
               <span @click.stop="removeRoom(item)" class="iconfont icon-guanbi"></span>
               <!-- <img draggable="false" :src="item.image" alt=""> -->
               <div style="font-size:15px;max-width:50px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.roomName"></div>
            </li>

             <li @click.stop="showUpload=!showUpload"><span class="iconfont icon-add" style="color:#666;font-size:48px;line-height:44px;text-align:center;cursor:pointer"></span></li>
           </ul>
        </div>
    </div>

    <Drawer title="" :closable="false" v-model="showUpload" class="facility-draw">
         <div class="upload-win-content">
            <!-- <input type="file" @change.stop="uploadImage"/>  -->
            <div style="margin-bottom:20px"><span style="padding-right:12px"></span><input style="width:200px" class="image-url" placeholder="" type="text" text="roomData" v-model="roomName"/></div>
            <div style="margin-bottom:20px">: </div>
            <div style="text-align:center">
              <img :src="imgUrlRight" @click.stop="showRight=!showRight"/>
              <img :src="imgUrlDown" @click.stop="showDown=!showDown"/>
              <img :src="imgUrlLeft" @click.stop="showLeft=!showLeft"/>
              <img :src="imgUrlUp" @click.stop="showUp=!showUp"/>
            </div>
            <div style="margin-bottom:20px; text-align:center"> 
              <span style="margin-right:20px;"><Button @click="redoRoomData"></Button></span>
              <span><Button @click="clearRoomData"></Button></span>
            </div>
            <!-- <div style="margin-bottom:20px"><span></span><input class="image-url" placeholder=": :500;:300;:500;:300 (cm)" type="text" text="roomData" v-model="roomInput"/> <span><Button @click="clearRoomData"></Button></span></div> -->
            <div style="margin-bottom:20px">
              <span></span> 
              <span  v-text= "roomInput"></span> 
            </div>
         </div>
         <div class="upload-win-footer">
            <button @click="addRoom"></button>
         </div>
    </Drawer>

    <Drawer title="" :closable="false" v-model="showRight" class="addroom-child-draw">
      <div class="upload-win-content">
          <div style="margin-bottom:20px"><span></span><input  class="image-url" placeholder="cm" type="text" text="rightLength" v-model="lineLength"/></div>
          <div style="margin-bottom:20px">
           <span></span>
            <RadioGroup v-model="hasSlope">
                <Radio label="0"></Radio>
                <Radio label="1"></Radio>
            </RadioGroup>
          </div>
          <div style="margin-bottom:20px" v-if="hasSlope == 1"  >
            <div style="margin-bottom:20px">
              <span></span><input  class="slope-length-input" v-model="slope.length" placeholder="" type="text" text="name" />
              <img class = "tip-img" :src="imgUrlRightTip"/>
            </div>
            <RadioGroup v-model="slope.direction">
                <Radio label="0"></Radio>
                <Radio label="1"></Radio>
            </RadioGroup>
          </div>
      </div>
      <div class="upload-win-footer">
          <button @click="addRoomLine(0)"></button>
      </div>
    </Drawer>

    <Drawer title="" :closable="false" v-model="showDown" class="addroom-child-draw">
      <div class="upload-win-content">
          <div style="margin-bottom:20px"><span></span><input  class="image-url" placeholder="cm" type="text" text="rightLength" v-model="lineLength"/></div>
          <div style="margin-bottom:20px">
           <span></span>
            <RadioGroup v-model="hasSlope">
                <Radio label="0"></Radio>
                <Radio label="1"></Radio>
            </RadioGroup>
          </div>
          <div style="margin-bottom:20px" v-if="hasSlope == 1"  >
            <div style="margin-bottom:20px">
              <span></span><input  class="slope-length-input" v-model="slope.length" placeholder="" type="text" text="name" />
              <img class = "tip-img" :src="imgUrlLeftTip"/>
            </div>
            <RadioGroup v-model="slope.direction">
                <Radio label="0"></Radio>
                <Radio label="1"></Radio>
            </RadioGroup>
          </div>
      </div>
      <div class="upload-win-footer">
          <button @click="addRoomLine(1)"></button>
      </div>   
    </Drawer>

    <Drawer title="" :closable="false" v-model="showLeft" class="addroom-child-draw">
      <div class="upload-win-content">
          <div style="margin-bottom:20px"><span></span><input  class="image-url" placeholder="cm" type="text" text="rightLength" v-model="lineLength"/></div>
          <div style="margin-bottom:20px">
           <span></span>
            <RadioGroup v-model="hasSlope">
                <Radio label="0"></Radio>
                <Radio label="1"></Radio>
            </RadioGroup>
          </div>
          <div style="margin-bottom:20px" v-if="hasSlope == 1"  >
            <div style="margin-bottom:20px">
              <span></span><input  class="slope-length-input" v-model="slope.length" placeholder="" type="text" text="name" />
              <img class = "tip-img" :src="imgUrlRightTip"/>
            </div>
            <RadioGroup v-model="slope.direction">
                <Radio label="0"></Radio>
                <Radio label="1"></Radio>
            </RadioGroup>
          </div>
      </div>
      <div class="upload-win-footer">
          <button @click="addRoomLine(2)"></button>
      </div>   
  </Drawer>

    <Drawer title="" :closable="false" v-model="showUp" class="addroom-child-draw">
      <div class="upload-win-content">
          <div style="margin-bottom:20px"><span></span></div>
          <div style="margin-bottom:20px"><span></span><input  class="image-url" placeholder="cm" type="text" text="rightLength" v-model="lineLength"/></div>
          <div style="margin-bottom:20px">
           <span></span>
            <RadioGroup v-model="hasSlope">
                <Radio label="0"></Radio>
                <Radio label="1"></Radio>
            </RadioGroup>
          </div>
          <div style="margin-bottom:20px" v-if="hasSlope == 1"  >
            <div style="margin-bottom:20px">
              <span></span><input  class="slope-length-input" v-model="slope.length" placeholder="" type="text" text="name" />
              <img class = "tip-img" :src="imgUrlLeftTip"/>
            </div>
            <RadioGroup v-model="slope.direction">
                <Radio label="0"></Radio>
                <Radio label="1"></Radio>
            </RadioGroup>
          </div>
      </div>
      <div class="upload-win-footer">
          <button @click="addRoomLine(3)"></button>
      </div>   
  </Drawer>
  </div>
</template>
<script>

import eventBus from '../eventbus';
import FacilitiesList from '../../public/Config';

export default {
  name: 'Toolbar',
  data(){
     return{
        currentPanel : 1,
        //room
        customize:[],
        showCustomize:false,
        showUpload:false,
        showRight:false,
        showDown:false,
        showLeft:false,
        showUp:false,
        hasSlope : "0", //"0"- "1"-
        roomData:[],
        roomInput : '',
        roomName:'',
        currentScale: 1,
        savedScale: 1,
        lineLength : '',
        //
        slope : {
          length: '',
          direction: '0'
        },
        //obstacle
        obstacles:[],
        obstacle : {
          width : '',
          height : '',
          name : '',
          kind: 2,
        },
        showObstacleDialog: false,
        showObstacleDetail: false,
        //facility
        facilities:[],
        facility : {
          kind : 3,
          name:'',
          id: '',
          width:'',
          height:'',
          cost:'',
          safe: {
            up:'',
            right:'',
            down:'',
            left:''
          },
          //0123
          face: "0"
        },
        showFacilityDialog: false,
        facilityDetail: {
          isShow: false,
          showAddButton: false
        },
        serchKeyWord:'',
        showSearchDialog: false,
        combines: [],
        shape:[
          {
            id:'flowChart',
            header:'',
            icon:[
              {
                path:'<svg style="left: 1px; top: 1px; width: 32px; height: 30px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="1.44" y="7.68" width="28.8" height="14.4"  fill="#ffffff" stroke="#000000" stroke-width="1.3" pointer-events="all"></rect></g></g><g></g><g></g></g></svg>',
                iconName:'',
                text:'rounded rectangle',
                command:'rectangle'
              },
              {
                path:'<svg style="left: 1px; top: 1px; width: 32px; height: 30px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="0.73" y="7.3" width="29.2" height="14.6" fill="none" stroke="white" pointer-events="stroke" visibility="hidden" stroke-width="9"></rect><rect x="0.73" y="7.3" width="29.2" height="14.6" fill="none" stroke="none" pointer-events="all"></rect></g><g style=""><g transform="translate(7,10)scale(0.73)"><foreignObject style="overflow:visible;" pointer-events="all" width="22" height="12"><div style="display: inline-block; font-size: 12px; font-family: Helvetica; color: rgb(0, 0, 0); line-height: 1.2; vertical-align: top; width: 24px; white-space: normal; overflow-wrap: normal; text-align: center;"><div xmlns="http://www.w3.org/1999/xhtml" style="display:inline-block;text-align:inherit;text-decoration:inherit;white-space:normal;">Text</div></div></foreignObject></g></g></g><g></g><g></g></g></svg>',
                iconName:'',
                text:'text',
                command:'text'
              },
              {
                path:'<svg style="left: 1px; top: 1px; width: 32px; height: 30px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="1.44" y="7.68" width="28.8" height="14.4" fill="#ffffff" stroke="#000000" stroke-width="1.3" pointer-events="all"></rect><path d="M 4.32 7.68 L 4.32 22.08 M 27.36 7.68 L 27.36 22.08" fill="none" stroke="white" stroke-width="9.3" stroke-miterlimit="10" pointer-events="stroke" visibility="hidden"></path><path d="M 4.32 7.68 L 4.32 22.08 M 27.36 7.68 L 27.36 22.08" fill="none" stroke="#000000" stroke-width="1.3" stroke-miterlimit="10" pointer-events="all"></path></g></g><g></g><g></g></g></svg>',
                iconName:'',
                text:'process',
                command:'preRect'
              },
            ]
          }
        ],
        imgUrlRight:require("../img/right.png"),
        imgUrlDown:require("../img/down.png"),
        imgUrlLeft:require("../img/left.png"),
        imgUrlUp:require("../img/up.png"),
        imgUrlLeftTip:require("../img/leftTip.png"),
        imgUrlRightTip:require("../img/rightTip.png"),  
     }
  },
  computed:{
    precustomize(){
        return this.customize.slice().splice(0,100);
     },
    preObstacle(){
        return this.obstacles.slice().splice(0,100);
     },
    preFacilities(){
      var facilities = this.facilities;
      if(this.serchKeyWord === '') {
        return facilities.slice();
      }
      let res = [];
      for(let i=0; i<facilities.length; i++) {
        if(facilities[i].name.startsWith(this.serchKeyWord) || facilities[i].id.startsWith(this.serchKeyWord)) {
          res.push(facilities[i]);
        }
      }
      return res;
     }
  },

  mounted(){
    var me = this;
    var strRoom=localStorage.getItem('customize-icon');
    var strObstacles=localStorage.getItem('obstacles-icon');
    var strFacilities=localStorage.getItem('facilities-icon');
    try{
      if(strRoom){
         var dataRoom=JSON.parse(strRoom);
         this.customize=dataRoom;
      }
      var door = {
        width : '60',
        height : '14',
        name : '',
        kind : 1
      }
      door.path = this.buildPath(door);
      var dataOb= strObstacles ? JSON.parse(strObstacles) : [];
      this.obstacles=[door, ...dataOb];

      var dataFa= strFacilities ? JSON.parse(strFacilities) : [];
      var lists = FacilitiesList;
      for(let i=0; i<lists.length; i++) {
        lists[i].path = this.buildPath(lists[i]);
        dataFa.push(lists[i]);
      }
      this.facilities=dataFa;
      
    }catch(e){
       window.console.log(e);
    }

    eventBus.$on('addCombine',(e)=>{
      var node = e.node;
      node.path = me.buildPath(node);
      me.combines.push(node);
      me.currentPanel = "2";
    })

    eventBus.$on('closeCombine',()=>{
      this.currentScale = this.savedScale;
    })

    eventBus.$on('deleteAll',()=>{
      this.currentScale = 1;
    });

    eventBus.$on('showInfoDrawWithNode',(e)=>{
      if(e.node.kind == '3') {
        Object.assign(me.facility, e.node.originData);
        this.facilityDetail.isShow = true;
        this.facilityDetail.showAddButton = false;
      }
    });
  },
  methods:{

    clearSearch () {
      this.serchKeyWord = '';
    },

    showFacilityDetail() {
      this.facilityDetail.isShow = !this.facilityDetail.isShow;
      this.facilityDetail.showAddButton = true;
    },

    showCombinePanel () {
      this.currentPanel = 1;
      this.showCombine = !this.showCombine;
      this.savedScale = this.currentScale;
      this.currentScale = 1;
      eventBus.$emit('showCombine', {isShow : true});
    },

    command(p){
      this.currentPanel = p;
    },

    clearRoomData () {
      this.roomData = [];
      this.roomInput = "";
    },

    redoRoomData () {
      this.roomData.pop();
      this.updateRoomInput();
    },

    addRoom(){
      if(this.roomData.length > 0 && this.roomName){
         var obj={
           roomName : this.roomName,
           roomData : this.roomData 
         };
         this.customize.push(obj);
         this.storeRoom();
         this.showUpload=false;
         this.showCustomize=false;
         this.roomData=[];
         this.roomName='';
      }
    },
    
    addObstacle() {
      if(this.obstacle.width && this.obstacle.height && this.obstacle.name){
      var obj = JSON.parse(JSON.stringify(this.obstacle));
         obj.path = this.buildPath(this.obstacle);
         this.obstacles.push(obj);
         this.storeObstacles();
         this.showObstacleDetail=false;
         this.showObstacleDialog=false;
         this.obstacle.width='';
         this.obstacle.height='';
         this.obstacle.name='';
      }
    },

    addFacility() {
      if(this.facility.name && this.facility.width && this.facility.height) {
         var obj = JSON.parse(JSON.stringify(this.facility));
         obj.path = this.buildPath(this.facility);
         this.facilities.push(obj);
         //this.storeFacilities();
         this.facilityDetail.isShow=false;
         this.showFacilityDialog=false;
         this.clearFacility();
      }
    },

    clearFacility() {
      this.facility.name = '';
      this.facility.id = '';
      this.facility.width = '';
      this.facility.height = '';
      this.facility.face = 0;
      this.facility.safe.up = '';
      this.facility.safe.down = '';
      this.facility.safe.left = '';
      this.facility.safe.right = '';
      this.facility.cost = '';
    },

    buildPath : function(f) {
      var buildName = function (name) {
          if(name.length > 10) {
            return name.slice(0, 5) + '...';
          } else {
            return name
          }
      };
      var buildParameter = function (f) {
          return f.width + '  ' + f.height + ' cm';
      };
      var width = 90, height = 35;
      if(f.kind === 1) {
        height = 20
      }
      if(f.kind === 2) {
        height = 35
      }
      if(f.kind === 2) {
        height = 35
      }
      if(f.kind === 5) {
        height = 35
      }
      if(f) {

        if(f.kind === 1) {
          let nameStr = '<text x="35" y="25" fill="black" font-size="16">'+ ''+'</text>';
          let paramStr = '<text x="2" y="50" fill="black" font-size="16">'+ buildParameter(f) +'</text>';
          return '<svg style="left: 1px; top: 1px; width: 100px; height: 50px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="1.44" y="7.68" width="' + width + '" height="' + height +'"  fill="#ffffff" stroke="#000000" stroke-width="1.3" pointer-events="all"></rect>' + nameStr + paramStr + '</g></g><g></g><g></g></g></svg>';
        }

        let nameStr = '<text x="5" y="30" fill="black" font-size="16">'+ buildName(f.name) +'</text>';
        let idStr = '';
        if(f.id) {
          idStr = '<text x="2" y="60" fill="black" font-size="16">'+ buildName(f.id) +'</text>';
        }
        let paramStr = '<text x="2" y="75" fill="black" font-size="14">'+ buildParameter(f) +'</text>';
        return '<svg style="left: 1px; top: 1px; width: 100px; height: 90px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="1.44" y="7.68" width="' + width + '" height="' + height +'"  fill="#ffffff" stroke="#000000" stroke-width="1.3" pointer-events="all"></rect>' + nameStr + idStr + paramStr + '</g></g><g></g><g></g></g></svg>';
      }
    },

    updateRoomInput : function() {
        var roomData = this.roomData;
        var str = '';
        var dirMap = {
          0 : '',
          1 : '',
          2 : '',
          3 : ''
        };
        roomData.forEach(r => {
          let sDir = r.slopeDirection === '1' ? '+' : '-';
          let sStr = !r.hasSlope ? '' : '(' + '' + sDir +  r.slopeLength + ')';
          str += dirMap[r.direction] + ' : ' + r.length + sStr + '    ';
        });
        this.roomInput = str;
    },

    addRoomLine : function(direction) {
      if(this.lineLength !== '') {
          this.roomData.push({
          direction : direction,
          length : this.lineLength,
          hasSlope : this.hasSlope === '1',
          slopeLength : this.slope.length,
          slopeDirection : this.slope.direction
        })
      }
      this.hasSlope = '0';
      this.slope.length = '';
      this.lineLength = '';
      this.showRight = false;
      this.showDown = false;
      this.showLeft = false;
      this.showUp = false;
      this.updateRoomInput();
    },

    storeRoom(){
      localStorage.setItem('customize-icon',JSON.stringify(this.customize));
    },
    storeObstacles(){
      localStorage.setItem('obstacles-icon',JSON.stringify(this.obstacles));
    },
    storeFacilities() {
      localStorage.setItem('facilities-icon',JSON.stringify(this.facilities));
    },
    removeRoom(item){
      var index=this.customize.indexOf(item);
      if(index>-1){
         this.customize.splice(index,1);
         this.storeRoom();
      }
    },

    removeObstacle(item){
      var index=this.obstacles.indexOf(item);
      if(index>-1){
         this.obstacles.splice(index,1);
         this.storeObstacles();
      }
    },

    removeFacility(item){
      var index=this.facilities.indexOf(item);
      if(index>-1){
         this.facilities.splice(index,1);
         this.storeFacilities();
      }
    },

    getRoomScale (roomData) {
      var maxSize = 0,
          lines = roomData,
          firstLine = lines[0],
          offSetX = 0,
          offSetY = 0;
      for(let i=0; i<lines.length; i++) {
          if(Number.parseFloat(lines[i].length) > maxSize) maxSize = Number.parseFloat(lines[i].length);
      }
      var windowWidth = window.innerWidth - 150; 
      var windowHeight = window.innerHeight - 150; 
      var min = Math.min(windowWidth, windowHeight);
      var scale = maxSize/min*1.1;
      offSetX = windowWidth/2 - firstLine.length/scale/2;
      if(firstLine.hasSlope && firstLine.slopeDirection === '0') {
        offSetY = firstLine.slopeLength/scale;
      }
      return {
        scale : scale,
        'offSetX' : offSetX,
        'offSetY' : offSetY
      };
    },

    buildRoomCommand(roomData, scaleObj){
        var res = [];
        var lines = roomData;
        var scale = scaleObj.scale;
        var startX = 0 + scaleObj.offSetX;
        var startY = 50 + scaleObj.offSetY;
        var lastX = startX;
        var lastY = startY;
        var lineWeight = 2;
        for(let i=0; i<lines.length; i++) {
            var lineCmd = {
              command:'line',
              kind: 0,
              scale: scale
            }
            var current = lines[i];
            var direct = current.direction;
            var origniaLength = current.length;
            var length = Number.parseFloat(origniaLength)/scale;
            var shape = {
              x1 : lastX,
              y1 : lastY
            }
            var textCmd = {
              style: {
                  text:  Number.parseInt(origniaLength) + 'cm',
                  fontSize: 15,
              },
              command:'text',
              position:[0,0],
              draggable: false,
            }
            if(textCmd.style.text >= 6) textCmd.style.fontSize = 12;
            lineCmd.text = length;

            switch(direct) {
              case 0 :
                if(!current.hasSlope) {
                  shape.x2 =  shape.x1 + length;
                  shape.y2 = shape.y1;
                } else {
                  //(l-x1)^2 + slopeLength^2 = length^2  
                  let sLength = Number.parseFloat(current.slopeLength)/scale;
                  let verticalLength = Math.sqrt(Math.pow(length, 2) - Math.pow(sLength, 2));
                  shape.x2 =  shape.x1 + verticalLength;
                  if(current.slopeDirection === '0') {
                    shape.y2 = shape.y1 - sLength;
                  } else {
                    shape.y2 = shape.y1 + sLength;
                  }
                }
                textCmd.x = (shape.x1 + shape.x2)/2 - 25;
                textCmd.y = (shape.y1 + shape.y2)/2 - 25;
                lineCmd.direction = 0;
                break;
              case 1 :
                if(!current.hasSlope) {
                  shape.y1 -= lineWeight;
                  shape.x2 =  shape.x1;
                } else {
                  //(l-x1)^2 + slopeLength^2 = length^2
                  let sLength = Number.parseFloat(current.slopeLength)/scale;
                  let verticalLength = Math.sqrt(Math.pow(length, 2) - Math.pow(sLength, 2));
                  shape.y2 =  shape.y1 + verticalLength;
                  if(current.slopeDirection === '0') {
                    shape.x2 = shape.x1 + sLength;
                  } else {
                    shape.x2 = shape.x1 - sLength;
                  }
                }
                shape.y2 = shape.y1 + length + lineWeight;
                textCmd.x = (shape.x1 + shape.x2)/2 + 5;
                textCmd.y = (shape.y1 + shape.y2)/2;
                lineCmd.direction = 1;
                break;              
              case 2 :
                if(!current.hasSlope) {
                  shape.x2 =  shape.x1 - length;
                  shape.y2 = shape.y1;
                } else {
                  //(l-x1)^2 + slopeLength^2 = length^2
                  let sLength = Number.parseFloat(current.slopeLength)/scale;
                  let verticalLength = Math.sqrt(Math.pow(length, 2) - Math.pow(sLength, 2));
                  shape.x2 =  shape.x1 - verticalLength;
                  if(current.slopeDirection === '0') {
                    shape.y2 = shape.y1 + sLength;
                  } else {
                    shape.y2 = shape.y1 - sLength;
                  }
                }
                textCmd.x = (shape.x1 + shape.x2)/2 - 25;
                textCmd.y = (shape.y1 + shape.y2)/2 + 5;
                lineCmd.direction = 2;
                break;
               case 3 :
                if(!current.hasSlope) {
                  shape.y1 += lineWeight;
                  shape.x2 =  shape.x1 ;
                } else {
                  //(l-x1)^2 + slopeLength^2 = length^2
                  let sLength = Number.parseFloat(current.slopeLength)/scale;
                  let verticalLength = Math.sqrt(Math.pow(length, 2) - Math.pow(sLength, 2));
                  shape.y2 =  shape.y1 - verticalLength;
                  if(current.slopeDirection === '0') {
                    shape.x2 = shape.x1 - sLength;
                  } else {
                    shape.x2 = shape.x1 + sLength;
                  }
                }
                shape.y2 = shape.y1 - length - lineWeight;
                textCmd.x = (shape.x1 + shape.x2)/2 - 60;
                textCmd.y = (shape.y1 + shape.y2)/2 - 2;
                lineCmd.direction = 3;
                break;               
            }
            lineCmd.shape = shape;
            res.push(lineCmd);
            res.push(textCmd);
            lastX = shape.x2;
            lastY = shape.y2;
        }
      return res; 
    },

    buildObstacleCommand(item){
      if(!item) return;
      var scale = this.currentScale,
          w = Number.parseFloat(item.width)/scale,
          h = Number.parseFloat(item.height)/scale,
          name = item.name;
      return {
        style: {
            fill: '#A9C4EB',
            stroke: '#333',
            lineWidth: 1,
            text: name,
            textFill: '#333',
            fontWeight: 'normal',
            fontStyle: '',
            textPosition: 'inside',
            fontSize: 12,
            opacity:1
        },
        kind : item.kind,
        shape: {
            width: w,
            height: h
        },
        z: 200,
        draggable: true,
        originData : item,
        command: "rectangle"
      }
    },

    buildFacilityCommand(item){
      if(!item) return;
      var scale = this.currentScale,
          w = Number.parseFloat(item.width)/scale,
          h = Number.parseFloat(item.height)/scale,
          name = item.name,
          face = item.face;
      return {
        style: {
            fill: '#fff',
            stroke: '#333',
            lineWidth: 1,
            text: name,
            textFill: '#333',
            fontWeight: 'normal',
            fontStyle: '',
            textPosition: 'inside',
            fontSize: 12,
            opacity:1
        },
        safe: {
          up : Number.parseFloat(item.safe.up),
          down : Number.parseFloat(item.safe.down),
          left : Number.parseFloat(item.safe.left),
          right : Number.parseFloat(item.safe.right)
        },
        face: face,
        kind : 3,
        shape: {
            width: w,
            height: h
        },  
        z: 200,
        originData : item,
        draggable: true,
        command: "preRect"
      }
    },


    buildCombineCommand(item){
      if(!item) return;
      var scale = this.currentScale,
          w = Number.parseFloat(item.width)/scale,
          h = Number.parseFloat(item.height)/scale,
          name = item.name,
          face = item.face,
          alignFace = item.alignFace;

        var buildNodes = function(nodes) {
          var res = []
          for(let i=0; i<nodes.length; i++) {
            res.push({
              width: nodes[i].data.shape.width/scale,
              height: nodes[i].data.shape.height/scale,
              name : nodes[i].style.text
            })
          }
          return res;
        }

      return {
        style: {
            fill: '#fff',
            stroke: '#333',
            lineWidth: 1,
            text: name,
            textFill: '#333',
            fontWeight: 'normal',
            fontStyle: '',
            textPosition: 'inside',
            fontSize: 12,
            opacity:1
        },
        safe: {
          up : Number.parseFloat(item.safe.up),
          down : Number.parseFloat(item.safe.down),
          left : Number.parseFloat(item.safe.left),
          right : Number.parseFloat(item.safe.right)
        },
        face: face,
        alignFace: alignFace,
        kind : 3, //
        shape: {
            width: w,
            height: h,
            nodes: buildNodes(item.nodes),
        },  
        z: 200,
        draggable: true,
        //originData : item,
        command: "combine"
      }
    },

    dragstart(e,item){
      //0 
      //2 
      //3 
      //4 
      //5 
      var cmd = '';
      if(item.roomData) {
        var s = this.getRoomScale(item.roomData);
        cmd = JSON.stringify(this.buildRoomCommand(item.roomData, s));
        this.currentScale = s.scale;
      } else if(item.kind === 2 || item.kind === 1){
        cmd = JSON.stringify(this.buildObstacleCommand(item));
      } else if(item.kind === 3){
        cmd = JSON.stringify(this.buildFacilityCommand(item));
      } else if(item.kind === 4){ 
        cmd = JSON.stringify(this.buildCombineCommand(item));
      } else if(item.kind === 5){ 
        cmd = JSON.stringify(this.buildCombineCommand(item));
      }
      if(cmd !== '') {
        e.dataTransfer.setData("addShape", cmd);
      }
    },

    uploadImage(e){
      var file=e.target.files[0];
      if(file){
       var type=file.type;
       var formData=new FormData();
       formData.append('smfile',file);
       if(['image/jpeg','image/png'].indexOf(type)>-1){
           this.ajax({
             method:'POST',
             url:'https://sm.ms//api/v2/upload',
             body:formData,
             succseeFn(res){
               window.console.log(res);
             },
             failFn(xhr){
               window.console.log(xhr);
             }

           })
       }
      }
    },
    ajax :function(options){
          //
          let method = options.method
          let url = options.url
          let body = options.body
          let succseeFn = options.succseeFn
          let failFn = options.failFn

          let request = new XMLHttpRequest();
          //
          request.open(method, url,true);
          request.setRequestHeader("Content-type","multipart/form-data");
          request.setRequestHeader('Authorization','');
          request.onreadystatechange = () => {
              if (request.readyState === 4) {
                  if (request.status >= 200 && request.status < 300) {
                      succseeFn.call(undefined, request.responseText)
                  } else if (request.status >= 400) {
                      failFn.call(undefined, request)
                  }
              }
          }
          request.send(body);
   }
 }

}
</script>

